import React, { useState } from 'react';
import styles from './SingleUserForm.module.scss';
import { IMAGES_URL } from '../../../../config';
import { Button, Form } from 'react-bootstrap';
import { useForm } from 'react-hook-form';
import { PACKAGE } from '../../../../consts/constants';

const SingleUserForm = ({ user, action }) => {
  const { handleSubmit, register } = useForm();
  const [countCheckings, setCountCheckings] = useState(0);

  const packageNumber = PACKAGE[user.package];

  const onSubmitCallback = (data) => {
    const transformedData = Object.entries(data).map(([id, checkBox]) => ({
      id,
      checkBox,
    }));

    action(transformedData);
  };

  return (
    <div className={styles.container}>
      <div className={styles.textWrapper}>
        <h2>Wane Informacje dotyczce Wyboru Zdj</h2>
        <p>
          Zasady wyboru zdj do obrbki i druku s proste i elastyczne. Moesz
          wybra swoje zdjcia w zalenoci od wybranego pakietu, ale pamitaj,
          e wybr jest jednorazowy, wic rozwa go dokadnie. Liczba zdj w
          Twoim wyborze jest ograniczona do ustalonej iloci, ale jeli zechcesz
          wybra wicej, moesz to zrobi za dodatkow opat. Dlatego warto
          przemyle swoje wybory i wybra te fotografie, ktre s dla Ciebie
          najwaniejsze. Dzikujemy za zaufanie i serdecznie pozdrawiamy!
        </p>
      </div>
      <Form
        onSubmit={handleSubmit(onSubmitCallback)}
        className={styles.formWrapper}
      >
        {user.pictures.map((item) => (
          <div className={styles.box} key={item.id}>
            <div className={styles.imageBox} key={item.id}>
              <img
                src={IMAGES_URL + item.name}
                alt={item.name}
                className={styles.image}
              />
            </div>
            <div className={styles.form}>
              <Form.Check
                {...register(item.id)}
                type="checkBox"
                label="Wybierz"
                onChange={(e) => {
                  if (e.target.checked) {
                    setCountCheckings(countCheckings + 1);
                  } else {
                    setCountCheckings(countCheckings - 1);
                  }
                }}
              />
            </div>
          </div>
        ))}
        <div className={styles.info}>
          <p>Moesz wybra {packageNumber} zdj</p>
          {countCheckings > packageNumber && (
            <p>kade nastpne bdzie dodatkowo patne</p>
          )}
          <p>Wybrano {countCheckings}</p>
        </div>
        <Button
          className={styles.confirmButton}
          type="submit"
          variant="primary"
        >
          potwierd wybr
        </Button>
      </Form>
    </div>
  );
};

export default SingleUserForm;
