import React from 'react';
import styles from './IntroductionSection.module.scss';
import { Container } from 'react-bootstrap';

const IntroductionSection = () => {
  return (
    <div className={styles.wrapper}>
      <Container className={styles.container}>
        <div className={styles.image}>
          <img
            alt={'dziecko'}
            src={process.env.PUBLIC_URL + '/images/introduction/1.jfif'}
          />
        </div>
        <div className={styles.text}>
          <p>Kada historia jest wyjtkowa.</p>
          <h2>
            ycie to nieustanna podr, a kady z nas ma swoj wasn,
            niepowtarzaln opowie do opowiedzenia.
          </h2>
          <p>
            Czasem zapominamy, jak wane jest zatrzymywanie chwil, ktre
            stanowi tkank naszego ycia. Dzieci rosn tak szybko, e czasem
            trudno nady za zmianami. Mog pomc Ci w uwiecznieniu tych
            drogocennych momentw w yciu Twojej rodziny, niezalenie od tego,
            czy jeste wyczekujcym, czy dowiadczonym rodzicem chccym
            upamitni pikne chwile codziennoci swoich dzieci. Razem moemy
            stworzy kapsu czasu pen wspomnie, ktre bd cieszy serca
            Waszej rodziny przez lata.
          </p>
        </div>
      </Container>
    </div>
  );
};

export default IntroductionSection;
