import React from 'react';
import { Button, Container } from 'react-bootstrap';
import styles from './MyOffer.module.scss';
import Heading from '../Heading/Heading';
import { useNavigate } from 'react-router-dom';

const MyOffer = () => {
  const navigate = useNavigate();

  const handlesubmit = (url) => {
    navigate(`/${url}`);
  };

  return (
    <div className={styles.section}>
      <Container className={styles.container}>
        <Heading text={'Oferta'} />
        <div className={styles.wrapper}>
          <div className={`${styles.item} ${styles.rightSide}`}>
            <img
              alt={'noworodek'}
              src={process.env.PUBLIC_URL + '/images/offer/noworodek.jfif'}
            />
            <div className={styles.infoRightSide}>
              <h2>sesja noworodkowa</h2>
              <p>
                Sesja noworodkowa to jedna z najpikniejszych pamitek, jakie
                moesz stworzy dla swojego dziecka. To niepowtarzalna okazja do
                uwiecznienia pierwszych dni ycia maluszka, ktre tak szybko
                mijaj. Warto wic zatrzyma je na duej, by mc do nich
                powraca w przyszoci.
              </p>
              <Button
                onClick={() => handlesubmit('sesja-noworodkowa')}
                variant="outline-secondary"
                size="lg"
              >
                dowiedz si wicej
              </Button>
            </div>
          </div>
          <div className={`${styles.item} ${styles.leftSide}`}>
            <img
              alt={'dziecko'}
              src={process.env.PUBLIC_URL + '/images/offer/dziecko.jfif'}
            />
            <div className={styles.infoLeftSide}>
              <h2>sesja dziecica</h2>
              <p>
                Sesja dziecica to niezapomniana przygoda, ktra dostarczy
                mnstwo radoci i umiechu. To take niepowtarzalna okazja do
                uwiecznienia najpikniejszych chwil z ycia Twojego dziecka.
                Sesja dziecica to take wietny pomys na prezent dla bliskich.
              </p>
              <Button variant="outline-secondary" size="lg">
                dowiedz si wicej
              </Button>
            </div>
          </div>
          <div className={`${styles.item} ${styles.rightSide}`}>
            <img
              alt={'kobieta'}
              src={process.env.PUBLIC_URL + '/images/offer/kobieta.jfif'}
            />
            <div className={styles.infoRightSide}>
              <h2>sesja kobieca</h2>
              <p>
                Sesja kobieca to czas tylko dla Ciebie. To chwila, aby poczu
                si pikn i pewn siebie. Razem stworzymy magiczne ujcia
                podkrelajce Twoj wyjtkowo.
              </p>
              <Button variant="outline-secondary" size="lg">
                dowiedz si wicej
              </Button>
            </div>
          </div>
          <div className={`${styles.item} ${styles.leftSide}`}>
            <img
              alt={'kobieta w cizy'}
              src={process.env.PUBLIC_URL + '/images/offer/kobietaWciy.jfif'}
            />
            <div className={styles.infoLeftSide}>
              <h2>sesja ciowa (studio lub plener)</h2>
              <p>
                Sesja ciowa to wyjtkowy moment w oczekiwaniu na przyjcie
                Twojego dziecka. Moesz wybra sesj w komfortowym studio lub w
                malowniczym plenerze - decyzja naley do Ciebie.
              </p>
              <Button variant="outline-secondary" size="lg">
                dowiedz si wicej
              </Button>
            </div>
          </div>
          <div className={`${styles.item} ${styles.rightSide}`}>
            <img
              alt={'kobieta w cizy'}
              src={
                process.env.PUBLIC_URL +
                '/images/offer/oferta-sesja-ciazowa-noworodkowa.jpg'
              }
            />
            <div className={styles.infoRightSide}>
              <h2>sesja ciowa i noworodkowa</h2>
              <p>
                Kombinacja sesji ciowej i noworodkowej to sposb na
                uwiecznienie dwch niesamowitych momentw w yciu rodziny.
                Przygotujemy dla Ciebie wyjtkow opowie od oczekiwania na
                przyjcie dziecka a po pierwsze dni ycia maluszka.
              </p>
              <Button variant="outline-secondary" size="lg">
                dowiedz si wicej
              </Button>
            </div>
          </div>
          <div className={`${styles.item} ${styles.leftSide}`}>
            <img
              alt={'rodzina'}
              src={
                process.env.PUBLIC_URL +
                '/images/offer/oferta-sesja-rodzinna.jpg'
              }
            />
            <div className={styles.infoLeftSide}>
              <h2>sesja rodzinna w plenerze</h2>
              <p>
                Sesja rodzinna w plenerze to okazja do stworzenia piknych chwil
                razem z najbliszymi. Spdcie czas na wieym powietrzu i
                utrwalcie swoje wizi w niezapomniany sposb.
              </p>
              <Button variant="outline-secondary" size="lg">
                dowiedz si wicej
              </Button>
            </div>
          </div>
        </div>
      </Container>
    </div>
  );
};

export default MyOffer;
